import React from 'react';
import { Card } from 'antd';
import books from './books.json';

const { Meta } = Card;

function App() {
  return (
    <div style={{ display: 'flex', flexWrap: 'wrap', backgroundColor: '#1c2128' }}>
      {books.map((book) => (
            <Card
              hoverable
              style={{ width: 240, margin: '10px' }}
              cover={
                <img
                  alt="Book cover"
                  src={book.cover}
                  style={{ objectFit: 'cover', height: 350 }}
                />
              }
            >
              <Meta title={book.title} description={book.description} />
            </Card>
          ))}
    </div>  
  );
}

export default App;
